    mounted() {
        this.LoginAdm()
    },
    methods: {
        LoginAdm() {
            if (this.usuario == 'Miguel') {
                this.Login = false
            }
        },
        CerrarLogin() {
            window.location.reload();
        }
    }
 }
</script>

