<template>
  <v-app>
    <v-container fluid class="bg-GrisPerla">
      <v-row>
        <v-col>
          <div>
            <!-- Botones de accion -->
            <div class="d-flex align-center">
              <v-text class="text-h5"><v-icon>{{ Icons }}</v-icon> {{ DiadellMes }}</v-text>
              <div class="d-flex align-center justify-center py-3 px-10 bg-white"
                style="border: 1px solid white; border-radius: 20px; width: max-content; margin: 0 auto;">
                <v-btn class="mx-4"><v-icon>mdi-chevron-left</v-icon> Anterior</v-btn>
                <v-text>Del {{ this.date.toLocaleDateString()}} hazta el {{ this.date.toLocaleDateString()}}</v-text>
                <v-btn class="mx-4">Siguiente <v-icon>mdi-chevron-right</v-icon></v-btn>
              </div>
            </div>
            <!-- Calendario por semana -->
            <div class="d-flex justify-center my-5 px-2" style="flex-direction: row;">
              <v-row>
                <v-col cols="12" sm="4" class="px-1  py-1" v-for="(items, index) in DataMenu" :key="index">
                  <v-card style="height: 370px;" class="px-5 py-2">
                    <v-card-title>{{ items.dia }}:</v-card-title>
                    <v-table v-if="items.menu.length > 0">
                      <thead>
                        <tr>
                          <th class="text-left">
                            Alimento
                          </th>
                          <th class="text-left">
                            Precio
                          </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="(menu, index) in items.menu" :key="index">
                          <td style="height: 35px;" class="d-flex align-center">
                            <input type="checkbox" id="myCheckbox" class="mx-1" v-model="Active">
                            <label for="myCheckbox">{{ menu.alimento}}</label>
                          </td>
                          <td style="height: 35px;">${{ menu.precio }}</td>
                        </tr>
                      </tbody>
                      <v-card-actions style="margin: 0 auto;">
                        <v-btn @click="Activechecked">Todos</v-btn>
                        <v-btn @click="Desactivecheked">Ninguno</v-btn>
                      </v-card-actions>
                    </v-table>
                    <div class="d-flex" style="flex-direction: column;" v-else>
                      <v-text>Esperando la publicación del menú</v-text>
                      <div class="d-flex align-center"><input type="checkbox" id="myCheckbox" class="mx-1"><label
                          for="myCheckbox">Prereserva</label></div>
                    </div>
                    <div class="d-flex px-3 " style="justify-content: space-between;">
                      <v-text>Precio:</v-text>
                      <v-text>${{ }}</v-text>
                    </div>
                  </v-card>
                </v-col>
                <v-col sm="4" class="px-1  py-1">
                  <!-- Resumen de la semana -->
                  <v-card class="px-5 py-2" style="height: 370px; ">
                    <v-card-title>
                      Resumen de la semana:
                    </v-card-title>
                    <v-table>
                      <thead>
                        <tr>
                          <th class=" text-left">
                    Día de la semana
                    </th>
                    <th class="text-left">
                      Precio
                    </th>
                    </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(item, index) in days" :key="index">
                        <td style="height: 30px;">{{ item }}</td>
                        <td style="height: 30px;">$0.00</td>
                      </tr>
                      <tr>
                        <td style="height: 30px;">Gastos de la semana</td>
                        <td style="height: 30px;">$0.00</td>
                      </tr>
                    </tbody>
                    </v-table>
                  </v-card>
                </v-col>
                <!-- Calendario -->
                <v-col sm="4" class="py-1">
                  <v-card style="max-height: 370px;  overflow: hidden;scrollbar-width: none;" class="overflow-y-auto">
                    <v-card-title>{{ DiadellMes}} del {{ date.getMonth() }}/{{ date.getFullYear() }}</v-card-title>
                    <VCalendar></VCalendar>
                  </v-card>
                </v-col>
              </v-row>
            </div>
          </div>
        </v-col>
      </v-row>
    </v-container>
  </v-app>

</template>

<script>
import VCalendar from './VCalendar.vue';
export default {
  components: { VCalendar },
  props: ['DiadellMes' ,'DataMenu' , 'Icons'],
  data() {
    return {
      Active:false,
      days: ['Lunes', 'Martes', 'Miercoles', 'Jueves', 'Viernes', 'Sábado', 'Domingo'],
      date: new Date(),
      DataMenu : this.DataMenu,
    }
  },
  methods: {
    Activechecked() {
        this.Active = true;
    },
  Desactivecheked() {
      this.Active = false;
    }
    
  }
} 

</script>

