import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Login from '../views/Login.vue'
import PaneldeAdministracion from '@/views/PaneldeAdministracion.vue'
import Estadisticas from '@/views/Estadisticas.vue'
import Desayuno from '@/views/Desayuno.vue'
import Almuerzo from '@/views/Almuerzo.vue'
import Comida from '@/views/Comida.vue'
const routes = [
  {
    path: "/",
    redirect: { name: "Login" },
    
  },
  {
    path: "/homeview",
    name: "HomeView",
    component: HomeView,
  },
  {
    path: "/Login",
    name: "Login",
    component: Login,
  },
  {
    path: "/paneldeadministacion",
    name: "Panel de Administracion",
    component: PaneldeAdministracion,
  },
  {
    path: "/estadisticas",
    name: "Estadisticas",
    component: Estadisticas,
  },
  {
    path: "/desayuno",
    name: "Desayuno",
    component: Desayuno,
  },
  {
    path: "/comida",
    name: "comida",
    component: Comida,
  },
  {
    path: "/almuerzo",
    name: "Almuerzo",
    component: Almuerzo,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
