<template>
<Doughnut :data="data" :options="options" />
</template>

<script >
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js'
import { Doughnut } from 'vue-chartjs'
ChartJS.register(ArcElement, Tooltip, Legend)

export default {
  name: 'App',
  props:['labels'],
  components: {
    Doughnut
  },
  data() {
    return {
        data:{
        labels: this.labels,
  datasets: [
    {
      backgroundColor: ['#256DD3', '#5AA5F4', '#AFC5E8',],
      data: [40, 20, 80,]
    }
  ]
},

options :{
  responsive: true,
   
}
    }
  }
}
</script>

